
import Button from './Button'
import rpsHOC, { options } from './RPSHOC'
import Select from "react-select";

const RPSCompWithComp = (props) => {
    const {
        computerChoice,
        roundResult,
        userChoice,
        resetResult,
        userPoints,
        computerPoints,
        simulatePlay,
        setDropdownValue,
        winner } = props
    return (
        <div style={{ flex: 2 }}>
            <div>
                <h3>Computer with Computer</h3>
            </div>
            <div style={{ width: 300 }}>
              How many rounds do you want to play?   <Select options={options} onChange={(v) => setDropdownValue(v)} />
            </div>
            <div>
                <Button commonProps={styles.common} color={'#546FBC'} isDisabled={winner!==null} text={'Simulate Play'} onClick={() => simulatePlay()}></Button>
                <Button commonProps={styles.common} text={'Reset'} onClick={resetResult}></Button>
            </div>
            <div>
                <div style={styles.Container}>
                    <h2 style={styles.margin10}>Computer 1 Choice : {userChoice}</h2>
                    <h2 style={styles.margin10}>Computer 1 Point : {userPoints}</h2>
                </div>
                <div style={styles.Container}>
                    <h2 style={styles.margin10}>Computer 2 Choice : {computerChoice}</h2>
                    <h2 style={styles.margin10}>Computer 2 Point : {computerPoints}</h2>
                </div>
                <div>
                    <h2 style={styles.margin10}>Round Result : {roundResult === '1' ? 'Computer 1 Wins' : roundResult === '2' ? 'Computer 2 wins' : roundResult}</h2>
                </div>
                <div>
                {winner === 'p1' && <h1>Computer 1 Wins the game</h1>}
                {winner === 'p2' && <h1>Computer 2 Wins the game</h1>}
            </div>
            </div>
        </div>
    )
}

const styles = {
    Flex: {
        border: '1px solid red',
    },
    margin10: {
        margin: 10
    },
    Container: {
        display: 'flex',
    },
    common: {
        height: 50,
        textAlign: 'center',
        textDecoration: 'none',
        fontSize: 20,
        margin: 20,
        width:200,
        borderRadius:10,
        border:'none'
    },
}

export default rpsHOC(RPSCompWithComp);