
import Button from './Button'
import rpsHOC, { options, playElements } from './RPSHOC'
import Select from "react-select";
import styled from 'styled-components';

const RPSWithComputer = (props) => {
    const {
        setSelection,
        computerChoice,
        roundResult,
        userChoice,
        resetResult,
        userPoints,
        computerPoints,
        setDropdownValue,
        winner } = props
    return (
        <div >
            <div >
                <h1>Play with Computer</h1>
            </div>
            <div style={{ width: 500, display:'flex', alignItems:'center' }}>
                <label style={styles.label}>How many rounds do you want to play?</label>
                <div >
                    <Select options={options} onChange={(v) => setDropdownValue(v)} />
                </div>
            </div>
            <div style={styles.Container}>
                {
                    playElements.map((e, i) => {
                       return <Button key={e.value} 
                       isDisabled={winner !== null} 
                       commonProps={styles.common} 
                       color={e.color} 
                       style={styles.Rock} 
                       text={e.name} 
                       onClick={() => setSelection(e.value)}></Button>
                    })
                }
                <Button text={'Reset'} color={'#B5C4CB'} commonProps={{ ...styles.common, fontSize: 20 }} onClick={resetResult}></Button>
            </div>
            <div style={styles.Container}>
                <h2 style={styles.margin10}>User Choice : {userChoice}</h2>
                <h2 style={styles.margin10}>User Point : {userPoints}</h2>
            </div>
            <div style={styles.Container}>
                <h2 style={styles.margin10}>Computer Choice : {computerChoice}</h2>
                <h2 style={styles.margin10}>Computer Point : {computerPoints}</h2>
            </div>
            <div>
                <h2 style={styles.margin10}>Round Result : {roundResult === '1' ? 'User Wins' : roundResult === '2' ? 'Computer wins' : roundResult}</h2>
            </div>
            <div>
                {winner === 'p1' && <h1>User Wins the game</h1>}
                {winner === 'p2' && <h1>Computer Wins the game</h1>}
            </div>
        </div>
    )
}

const styles = {
    margin10: {
        margin: 10
    },
    Container: {
        display: 'flex',
    },
    common: {
        height: 50,
        textAlign: 'center',
        textDecoration: 'none',
        fontSize: 20,
        margin: 20,
        width:200,
        borderRadius:10,
        border:'none'
    },
    select: {
        width: 100,
        fontSize:20
    },
    label:{
        paddingRight:20,
        fontSize:20
    }
}

const Label = styled.label`
margin-right:20;
color:'red';
`


export default rpsHOC(RPSWithComputer);