import React, { Component } from "react"
import _ from 'lodash'

export const options = [
     { value: 3, label: 'Three' },
     { value: 5, label: 'Five' }, 
     { value: 10, label: 'Ten' }]
export const playElements = [
    { name: 'Rock', value: 'r', color: '#33FFBD' },
    { name: 'Paper', value: 'p', color: '#FFBD33' },
    { name: 'Scissor', value: 's', color: '#33A7FF' }]

const rpsHOC = (WrappedComponent) => {

    class RPSHOC extends Component {
        constructor(props) {
            super(props)
            this.state = {
                rounds: 0,
                userPoints: 0,
                computerPoints: 0,
                computerChoice: '',
                userChoice: '',
                roundResult: '',
                winner: null
            }
        }

        randomSelection = () => {
            return playElements[Math.floor(Math.random() * playElements.length)].value;
        }
        setDropdownValue = (v) => {
            this.resetResult()
            this.setState({ rounds: v.value })
        }
        resetResult = () => {
            this.setState({
                userPoints: 0,
                computerPoints: 0,
                computerChoice: '',
                userChoice: '',
                roundResult: '',
                winner: null
            })
        }
        setSelection = (userChoice) => {
            const compChoice = this.randomSelection()
            this.checkWinner(userChoice, compChoice);
        }

        checkWinner = (userChoice, compChoice) => {
            this.setState({
                computerChoice: _.find(playElements, { value: compChoice }).name,
                userChoice: _.find(playElements, { value: userChoice }).name
            })
            if (userChoice === compChoice) {
                this.setState({ roundResult: 'Draw' })
            }
            //Player 1 wins
            else if ((userChoice === 'r' && compChoice === 's') || (userChoice === 'p' && compChoice === 'r') || (userChoice === 's' && compChoice === 'p')) {
                this.setState({
                    roundResult: '1',
                    userPoints: this.state.userPoints + 1
                }, () => { this.setWinner() }
                )
            }
            //Player 2 wins
            else {
                this.setState({
                    roundResult: '2',
                    computerPoints: this.state.computerPoints + 1
                }, () => { this.setWinner() }
                )
            }
        }
        setWinner = () => {
            const { rounds } = this.state
            rounds !== 0 && this.setState({ winner: this.state.userPoints === rounds ? 'p1' : this.state.computerPoints === rounds ? 'p2' : null })
        }
        simulatePlay = () => {
            const Comp1Choice = this.randomSelection()
            const Comp2Choice = this.randomSelection()
            this.checkWinner(Comp1Choice, Comp2Choice);
        }

        render() {
            return <WrappedComponent
                setSelection={userChoice => this.setSelection(userChoice)}
                computerChoice={this.state.computerChoice}
                userChoice={this.state.userChoice}
                roundResult={this.state.roundResult}
                resetResult={this.resetResult}
                computerPoints={this.state.computerPoints}
                userPoints={this.state.userPoints}
                simulatePlay={this.simulatePlay}
                setDropdownValue={rounds => this.setDropdownValue(rounds)}
                winner={this.state.winner}
                {...this.props}
            />
        }
    }
    return RPSHOC
}

export default rpsHOC